<template>
  <ul>
    <li v-for="event in events" :key="event.id">
      <router-link :to='viewLink(event)'>{{ event.name }}</router-link>
    </li>
  </ul>
</template>

<script>
import axios from "axios";

export default {
  name: "EventListView",
  data() {
    return {
      events: null
    }
  },
  created() {
    this.loadEvents();
  },
  methods: {
    loadEvents() {
      axios.get("/api/admin/events")
        .then(response => {
          console.log(response);
          this.events = response.data;
        })
    },
    viewLink(event) {
      return {
        name: "Event",
        params: {
          eventId: event.id
        }
      }
    }
  }
}
</script>
