<template>
  <div v-if="event">
    <h2>{{ event.name }}</h2>
    <p>{{ event.description }}</p>

    <nav>
      <router-link :to="{name: 'EventModify', params: event.id}">Редактировать</router-link>
      |
      <router-link :to="{name: 'TaskAdd', params: event.id}">Добавить задание</router-link>
    </nav>

    <ul>
      <li v-for="task in event.tasks" :key="task.id">
        <router-link :to=taskViewLink(task)>{{ task.name }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'EventView',
  data() {
    return {
      event: null
    }
  },
  created() {
    this.loadEvent();
  },
  methods: {
    loadEvent() {
      const eventId = this.$route.params.eventId;
      axios.get("/api/admin/events/" + eventId)
        .then(response => {
          console.log(response);
          this.event = response.data;
        })
    },
    taskViewLink(task) {
      return {
        name: "Task",
        params: {
          eventId: task.eventId,
          taskId: task.id
        }
      };
    }
  }
}
</script>
